  min-width: 206px;
  padding: 25px 10px;
  outline: 2px solid #102654;
  outline-offset: -2px;
}
@media (hover: hover) {
  .tabs__element-button:hover:not(.focus-visible) {
    outline: none;
    color: #ffffff;
    background-image: linear-gradient(97.05deg, #ED0233 17.6%, #003EB7 95.31%);
  }
}

.tabs--unset .tabs__content {
  height: 100px;
}

.tabs--unset .tabs__element {
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}

.promo {
  background-color: #ffffff;
}

.promo__container {
  max-width: 1160px;
  margin: 0 auto;
  display: flex;
  padding: 120px 0 106px;
}
@media (max-width: 1199px) {
  .promo__container {
    padding: 101px 45px;
    justify-content: center;
  }
}
@media (max-width: 767px) {
  .promo__container {
    padding: 71px 15px;
    display: block;
  }
}

.promo__image-container {
  flex-shrink: 0;
}

@media (max-width: 1199px) {
  .promo__image-container img {
    width: 386px;
    height: 292px;
  }
}
@media (max-width: 767px) {
  .promo__image-container img {
    width: 100%;
    height: auto;
  }
}

.promo__text-container {
  background-color: #ED0233;
  padding: 81px 73px 10px 77px;
  display: grid;
  grid-template-columns: 201px 100px;
  grid-template-rows: repeat(3, min-content);
  column-gap: 50px;
}
@media (max-width: 1199px) {
  .promo__text-container {
    grid-template-columns: 117px 60px;
    padding: 47px 42px 10px 45px;
    column-gap: 29px;
  }
}
@media (max-width: 767px) {
  .promo__text-container {
    justify-content: center;
    padding-bottom: 49px;
  }
}

.promo__text-container p {
  grid-column: 1/-1;
  margin: 0 0 49px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 120%;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .promo__text-container p {
    font-size: 12px;
    margin-bottom: 32px;
  }
}

.promo__text-container h3 {
  margin: 7px 0 0 14px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 82px;
  line-height: 96%;
  letter-spacing: 0.03em;
  color: #ffffff;
}
@media (max-width: 1199px) {
  .promo__text-container h3 {
    font-size: 48px;
    margin: 5px 0 0 8px;
  }
}

.promo__link {
  grid-column: 1/-1;
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: #ffffff;
  background-color: transparent;
  text-align: center;
  border: 4px solid #ffffff;
  padding: 24px 10px;
  justify-self: left;
  min-width: 346px;
}
@media (hover: hover) {
  .promo__link:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
.promo__link.focus-visible:focus {
  outline: 2px solid #000000;
}
@media (max-width: 1199px) {
  .promo__link {
    font-size: 10px;
    line-height: 12px;
    min-width: 202px;
    border: 2px solid #ffffff;
    padding: 13px 10px;
  }
}

.promo__date-container {
  display: flex;
  flex-direction: column;
  gap: 19px;
  margin-bottom: 22px;
}
@media (max-width: 1199px) {
  .promo__date-container {
    gap: 6px;
    margin-bottom: 8px;
  }
}

.promo__date-wrapper {
  display: grid;
  grid-template-columns: repeat(2, min-content);
  grid-template-rows: repeat(2, min-content);
  row-gap: 10px;
}
@media (max-width: 1199px) {
  .promo__date-wrapper {
    row-gap: 5px;
  }
}

.promo__date-wrapper-title {
  position: relative;
  grid-column: 1/-1;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 150%;
  color: #ffffff;
}
.promo__date-wrapper-title::after {
  content: "";
  position: absolute;
  height: 1px;
  left: 0;
  right: 0;
  background-color: #ffffff;
  bottom: -5px;
}
@media (max-width: 1199px) {
  .promo__date-wrapper-title {
    font-size: 12px;
  }
  .promo__date-wrapper-title::after {
    bottom: -2px;
  }
}

.promo__date-wrapper-value {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 40px;
  line-height: 100%;
  color: #ffffff;
  margin-right: 7px;
}
.promo__date-wrapper-value--time {
  margin-right: 0;
}
@media (max-width: 1199px) {
  .promo__date-wrapper-value {
    font-size: 24px;
    margin-right: 4px;
  }
}

.promo__date-wrapper-description {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: #ffffff;
  align-self: center;
}
@media (max-width: 1199px) {
  .promo__date-wrapper-description {
    font-size: 10px;
    line-height: 120%;
  }
}

